.consoleLine {
  display: grid;
  grid-template-columns: 50px 1fr;
}

.consoleLineText {
  display: flex;
  font-size: 18px;
  text-align: start;
}

.directoryIcon {
  width: 20px;
  padding-top: 24px;
}

.active {
  background-color: #24e64c;
  color: white;
}
/* These are styles that are added when files start getting deleted */
