.consoleBackground {
  background-color: rgb(30, 30, 30);
  min-height: 100vh;
  width: 100%;
}

.consoleBlack {
  background-color: black;
  min-height: 100vh;
  width: 100%;
}

.console {
  height: 90vh;
  width: 80vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 33%;
}

.heading {
  margin-left: 30px;
}
.contentContainer-directory {
  align-items: normal;
  display: flex;
  width: 80%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.contentContainer-file {
  align-items: normal;
  justify-content: center;
  display: flex;
  width: 120%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-left: 10px;
}

.contentContainer-file:focus {
  outline: none;
}
.contentContainer-directory:focus {
  outline: none;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.contentContainer::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.contentContainer {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

html {
  height: 100%;
}
